
body {
   background: whitesmoke;
}
#app {
   font-family: 'Avenir', Helvetica, Arial, sans-serif;
   text-align: center;
}
.calc {
   width: 320px;
   height: 600px;
   padding: 20px;
   border-radius: 10px;
   margin: 20px auto;
   font-size: 16px;
   background-color: #e0e0e0;
   -webkit-box-shadow: 8px 8px 14px #a4a4a4;
           box-shadow: 8px 8px 14px #a4a4a4;
}
.calc__display {
   border-bottom: 1px solid #eee;
   height: 40%;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-orient: vertical;
   -webkit-box-direction: normal;
       -ms-flex-direction: column;
           flex-direction: column;
}
.calc__display_content {
   position: relative;
   -webkit-box-flex: 1;
       -ms-flex-positive: 1;
           flex-grow: 1;
   text-align: right;
   word-break: break-all;
   font-size: 24px;
   color: #757575;
}
.calc__display_content div {
   position: absolute;
   right: 0;
   bottom: 0;
}
.calc__display_result {
   display: inline-block;
   padding: 8px 0;
   height: 36%;
   text-align: right;
   font-size: 52px;
   overflow: hidden;
   outline: none;
   color: #414141;
   word-break: break-all;
}
.calc__mode {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
   -webkit-box-pack: space-evenly;
       -ms-flex-pack: space-evenly;
           justify-content: space-evenly;
}
.calc__button {
   margin: 5px;
   width: 60px;
   height: 60px;
   outline: none;
   border: 0px;
   border-radius: 50%;
   color: #242323b9;
   cursor: pointer;
   -webkit-box-shadow:  8px 8px 14px #a4a4a4,
               -8px -8px 14px #ffffff;
           box-shadow:  8px 8px 14px #a4a4a4,
               -8px -8px 14px #ffffff;
}
.calc__button:active {
   -webkit-transform: translateY(2px);
           transform: translateY(2px)
}
.calc__button:hover {
   -webkit-box-shadow: 14px 14px 14px #a4a4a4,
              -10px -10px 14px #ffffff;
           box-shadow: 14px 14px 14px #a4a4a4,
              -10px -10px 14px #ffffff;
}
.equal-sign {
   width: 125px;
   border-radius: 50%;
}



* {
    padding: 0;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  *::after,
  *::before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  a {
    color: inherit;
    text-decoration: none;
  }
  
  ol,ul {
    list-style: none;
  }